import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import useForm from "../../hooks/useForm";

const RedeemCard = () => {
  const onSubmitHandler = (data) => {
    console.log(data);
    clearForm();
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    {},
    onSubmitHandler
  );

  return (
    <div className="mt-8">
      <form
        onSubmit={handleSubmit}
        className="flex flex-wrap items-end gap-x-8 gap-y-2"
      >
        <InputBox
          label="redeem gift card"
          name="giftCode"
          value={formData.giftCode}
          onChange={handleChange}
          placeholder="enter your gift code"
          className="w-60 h-10 border-2 border-secondary rounded-md px-4 py-2 text-sm placeholder:text-sm"
        />
        <Button className="h-10 px-4 py-2 rounded-md">redeem</Button>
      </form>
    </div>
  );
};

export default RedeemCard;
